export const addOne = function (num) {
  return Number(num) + 1
}

export const sendstatus = function (val) {
  // 1.2.3.4. 5. 
  const sendsta = {
    1: '',
    2: '',
    3: '',
    4: '',
    5: ''
  }
  return sendsta[val]
}

